import PropTypes from "prop-types";
import { CategoriesListAction } from "../../store/slices/HomePageSlice/HomePageSlice";
import { Link } from "react-router-dom";
// import { useTranslation } from "react-i18next";
import clsx from "clsx";
import { useEffect,useState
 } from "react";
import { useDispatch,useSelector } from "react-redux";

const NavMenu = ({ menuWhiteClass, sidebarMenu }) => {
  // const { t } = useTranslation();
  const [categoriesList, setcategoriesList] = useState([])
  const HomeReducer = useSelector((state) => state.HomeReducer)
  const dispatch = useDispatch()

  useEffect(() => {
    if(HomeReducer.categoriesListRes!=null){
      setcategoriesList(HomeReducer.categoriesListRes)
    }
  }, [HomeReducer.categoriesListRes])
  

  useEffect(() => {
   dispatch(CategoriesListAction())
  }, [])
  
  console.log("category",categoriesList)
  return (
    <div
      className={clsx(sidebarMenu
          ? "sidebar-menu"
          : `main-menu ${menuWhiteClass ? menuWhiteClass : ""}`)}
    >
      <nav>
        <ul>
          {categoriesList?.slice(0,6).map((item,idx)=>{

            return(  <li>
              <Link to={process.env.PUBLIC_URL + "/products" + "/" + item?.category_slug + "/" + item?.category_id}>
                {item?.category_name}
                {sidebarMenu ? (
                  <span>
                    <i className="fa fa-angle-right"></i>
                  </span>
                ) : (
                  <i className="fa fa-angle-down" />
                )}
              </Link>
                <ul className="submenu">
                  {item?.child?.map((item,idx)=>{

                    return( <li>
                      <Link to={process.env.PUBLIC_URL + "/products" + "/" + item?.category_slug+ "/" + item?.category_id}>
                        {item?.category_name}
                      </Link>
                    </li>)
                  })}
              </ul>
            </li>)
          })
        
          }
        </ul>
      </nav>
    </div>
  );
};

NavMenu.propTypes = {
  menuWhiteClass: PropTypes.string,
  sidebarMenu: PropTypes.bool,
};

export default NavMenu;
